<template>

  <div class="main">
     <div class="title">
       <a v-for="a in pData" @click="doindex()">{{a}}></a>
     </div>
    <div class="body">
      <h>{{bodyData.hDa}}</h>

      <div class="intro">
       <p>
         <span>{{bodyData.introData.title}}</span>
         <span>{{bodyData.introData.author}} {{bodyData.introData.sour}} {{bodyData.introData.read}}</span>
       </p>
        <!--listen-->
       <p class="listen">
         <a href="">
           <img src="../../static/images/s1.png" alt="" style="width:30px ;">
           
         </a>
       </p>
     </div>

      <div class="link">
        <div class="link-l">
          <img :src="bodyData.linkData.pic" style="width:30px ;">
          <p class="huin">
            <span>{{bodyData.linkData.tit}}</span>
            <span>{{bodyData.linkData.lit}}</span>
          </p>
        </div>
        <a href="">{{bodyData.linkData.but}}</a>
      </div>

      <p class="lit">{{bodyData.norData}}</p>
      <p class="mor">{{bodyData.norData}}</p>
      <img :src="bodyData.imgDa"  style="width:100% ;">
      <table>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="p in bodyData.tableData">
          <td>{{p.cla}}</td>
          <td>{{p.breed}}</td>
          <td>{{p.avera}}</td>
          <td>{{p.unit}}</td>
          <td>{{p.addr}}</td>
        </tr>
      </table>
      <img :src="bodyData.imgDa1"  style="width:100% ;">
      <div class="upDown">
        <p v-for="p in bodyData.upDoData.pData">{{p}}</p>
        <img :src="bodyData.upDoData.pic" style="width:100% ;">

        <p>
          {{bodyData.upDoData.A}}<span style="color:#ff8000">{{bodyData.upDoData.B}}</span>{{bodyData.upDoData.C}}
        </p>
        <h3><span style="color:#ff8000">{{bodyData.clas}}</span><span style="color:#ff8000">{{bodyData.from}}</span></h3>
      </div>


    </div>

  </div>

</template>

<script>
    export default {
        name: "detail",
      components:{},
      methods:{
          doindex(){
            this.$router.push({
              path:"/"
            })
          }
      },
      data(){
          return{
            pData:["","",""],
            bodyData:{
              hDa:"2018",
              introData:{
                title:"2018-10-30 20:50:08 ",
                author:"",
                sour:"",
                read:"46"
              },
              linkData:{
                "pic":"./static/images/p1.jpeg",
                "tit":"",
                "lit":"",
                "but":""
              },
              norData: "?",
              imgDa:"./static/images/pig.jpg",
              tableData:[
                {"cla":"","breed":"","avera":"12","unit":"","addr":""},
                {"cla":"","breed":"","avera":"8","unit":"","addr":""},
                {"cla":"","breed":"","avera":"7","unit":"","addr":""},
                {"cla":"","breed":"","avera":"6","unit":"","addr":""},
                {"cla":"","breed":"","avera":"10","unit":"","addr":""},
                {"cla":"","breed":"","avera":"12","unit":"","addr":""},
                {"cla":"","breed":"","avera":"12","unit":"","addr":""},
                {"cla":"","breed":"","avera":"8","unit":"","addr":""},
                {"cla":"","breed":"","avera":"7","unit":"","addr":""},
                {"cla":"","breed":"","avera":"6","unit":"","addr":""},
                {"cla":"","breed":"","avera":"10","unit":"","addr":""},
                {"cla":"","breed":"","avera":"12","unit":"","addr":""}
              ],
              imgDa1:"./static/images/pig2.jpg",
              upDoData:{
                pData:[
                  "",
                  "4.4-5.5",
                  "0.14.7-6.7",
                  "0.1,5.6-8.6",
                  "0.050.16.0-8.4",
                  "0.050.050.055.5-8.0",
                  "0.16.2-9.0 ",
                  "0.050.156.2-8.5"
                  ],
                pic:"./static/images/pig3.jpg",
                A:" ",
                B:"",
                C:"",
                },
              clas:"",
              from:""
            }
          }
      }
    }
</script>

<style lang="scss" scoped>

  .main{
    font-size: .12rem;
    display: flex;
    flex-direction: column;
    background: white;
    .title{
      background: #f5f5f5;
      display: flex;
      padding: .12rem;
      a{
       font-size: .12rem;
     }
    }
    .body{
      padding: 18px 14px;
      h{
        font-size: .18rem;
        padding-top: .09rem ;
      }
      .intro{
        display: flex;
        justify-content: space-between;
        padding: .1rem 0 .15rem 0;
        p{
          display: flex;
          flex-direction: column;
          font-size: .08rem;
          color: #999999;
        }
        .listen{
          /*color: darkgreen;*/
          a{
            display: flex;
            align-items: center;
            color: #66bb47;
            img{
              padding-right: 6px;
            }
          }
        }
      }
      .link{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: .16rem;
        .link-l{
          display: flex;
          align-items: center;

          img{
            border: 1px solid #f0f0f0;
            border-radius: 50%;
            margin-right: 10px;
          }
          .huin{
            display: flex;
            font-size: .06rem;
            flex-direction: column;
            span:first-of-type{
              font-size: .08rem;
            }
          }
        }
        a{
          padding: 5px 10px;
          background: #3897f0;
          border-radius: 2px;
          color: white;
        }
      }
      .lit{
        background: #f5f5f5;
        color: #666666;
        border-radius: 4px;
        padding: .08rem .07rem;
        font-size: 14px;
        line-height: 20px;
      }
      .mor{
        text-indent: 2em;
        font-size: 16px;
        color: #000;
        margin-top: .1rem;
        line-height: 28px;
      }
      table{
        width: 100%;
        font-size: 16px;
        tr{
          th,td{
            width: 20%;
            display: table-cell;
            text-align: center;
          }

        }
      }
      .upDown{
        p,h3{
          text-indent: 2em;
          font-size: .17rem;
          line-height: .26rem;
        }
        h3{
          color: #e53333;
        }
      }
    }


  }


</style>



